<template>
  <div class="print-box">
    <router-view/>
    <el-button @click="printPage" v-if='buttonShow' class="print-button">开始打印</el-button>
  </div>
</template>
<script>
export default {
  data () {
    return {
      buttonShow: true
    }
  },
  methods: {
    printPage(){
      this.buttonShow = false
      let timer = window.setTimeout(()=>{
        window.print()
        clearTimeout(timer)
        this.buttonShow = true
      },120)
    }
  }
}
</script>

<style lang="less">
.print-box{
  width: 100%;
  min-height: 100vh;
  font-size: 16px;
  background: #fff;
}
.print-button{
  margin: 30px;
}
</style>




